:root {
  --color-white: #fff;
  --color-light-blue-100: #e0f2fe;
  --color-light-blue-200: #f0f4f8;
  --color-blue-300: #60a5fa;
  --color-blue-500: #3b82f6;
  --color-blue-600: #2563eb;
  --color-dark-blue-700: #1e3a8a;
  --color-dark-blue-800: #1d4ed8;
  --color-gray-100: #f8fafc;
  --color-gray-200: #e2e8f0;
  --color-gray-300: #cbd5e1;
  --color-gray-500: #475569;
  --color-gray-700: #334155;
  --color-red-100: #fee2e2;
  --color-red-500: #ef4444;
  --shadow-sm: 0 2px 8px #0000000c;
  --shadow-md: 0 4px 10px #0000000c;
  --shadow-lg: 0 10px 25px #00000019;
  --shadow-button: 0 4px 10px #3b82f64c;
  --shadow-input-focus: 0 0 0 3px #60a5fa4c;
  --spacing-xs: .5rem;
  --spacing-sm: .8rem;
  --spacing-md: 1rem;
  --spacing-lg: 1.5rem;
  --spacing-xl: 2rem;
  --spacing-xxl: 2.5rem;
  --spacing-3xl: 3.5em;
  --border-radius-sm: .75rem;
  --border-radius-md: 1rem;
  --border-radius-lg: 1.5rem;
  --font-size-sm: .875rem;
  --font-size-base: 1rem;
  --font-size-md: 1.125rem;
  --font-size-lg: 1.875rem;
}

body {
  background-color: var(--color-light-blue-200);
  min-height: 100vh;
  padding-bottom: var(--spacing-xl);
  box-sizing: border-box;
  flex-direction: column;
  align-items: center;
  font-family: Inter, sans-serif;
  display: flex;
}

.top-menu {
  z-index: 1000;
  width: 100%;
  position: sticky;
  top: 0;
}

.container {
  background-color: var(--color-white);
  border-radius: var(--border-radius-lg);
  box-shadow: var(--shadow-lg);
  padding: var(--spacing-xxl);
  gap: var(--spacing-lg);
  width: 100%;
  max-width: 32rem;
  margin-top: var(--spacing-xl);
  flex-direction: column;
  display: flex;
}

input[type="text"], input[type="password"] {
  border: 2px solid var(--color-gray-300);
  border-radius: var(--border-radius-sm);
  padding: var(--spacing-sm) var(--spacing-md);
  font-size: var(--font-size-base);
  width: 100%;
  transition: border-color .2s ease-in-out;
}

input[type="text"]:focus, input[type="password"]:focus {
  border-color: var(--color-blue-300);
  box-shadow: var(--shadow-input-focus);
  outline: none;
}

button {
  background-color: var(--color-blue-500);
  color: var(--color-white);
  border-radius: var(--border-radius-sm);
  padding: var(--spacing-sm) var(--spacing-lg);
  font-size: var(--font-size-base);
  cursor: pointer;
  box-shadow: var(--shadow-button);
  border: none;
  font-weight: 600;
  transition: background-color .2s ease-in-out, transform .1s ease-in-out;
}

#get-weather-btn {
  height: var(--spacing-3xl);
}

button:hover {
  background-color: var(--color-blue-600);
  transform: translateY(-2px);
}

button:active {
  transform: translateY(0);
}

.weather-display {
  background-color: var(--color-light-blue-100);
  border-radius: var(--border-radius-md);
  padding: var(--spacing-lg);
  text-align: center;
  color: var(--color-dark-blue-700);
  box-shadow: var(--shadow-md);
}

.weather-display h2 {
  font-size: var(--font-size-lg);
  margin-bottom: var(--spacing-xs);
  color: var(--color-dark-blue-800);
  font-weight: 700;
}

.weather-display p {
  font-size: var(--font-size-md);
  margin-bottom: var(--spacing-xs);
  line-height: 1.5;
}

.error-message {
  background-color: var(--color-red-100);
  color: var(--color-red-500);
  border-radius: var(--border-radius-sm);
  padding: var(--spacing-md);
  text-align: center;
  font-weight: 600;
}

.loading-spinner {
  border: 4px solid #00000019;
  border-left-color: var(--color-blue-500);
  border-radius: 50%;
  width: 24px;
  height: 24px;
  margin: 0 auto;
  animation: 1s linear infinite spin;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.forecast-hourly-container {
  gap: var(--spacing-md);
  scroll-snap-type: x mandatory;
  padding: var(--spacing-lg);
  scrollbar-width: thin;
  scrollbar-color: var(--color-blue-300) var(--color-gray-200);
  flex-wrap: nowrap;
  width: 100%;
  margin: 0;
  display: flex;
  overflow-x: auto;
}

.forecast-hourly-container::-webkit-scrollbar {
  height: 8px;
}

.forecast-hourly-container::-webkit-scrollbar-track {
  background: var(--color-gray-200);
  border-radius: 4px;
}

.forecast-hourly-container::-webkit-scrollbar-thumb {
  background-color: var(--color-blue-300);
  border-radius: 4px;
}

.forecast-hour-card {
  background-color: var(--color-gray-100);
  border: 1px solid var(--color-gray-200);
  border-radius: var(--border-radius-sm);
  width: 140px;
  min-width: 140px;
  padding: var(--spacing-md);
  text-align: center;
  box-shadow: var(--shadow-sm);
  scroll-snap-align: start;
  flex: none;
  transition: transform .2s, box-shadow .2s;
}

.forecast-hour-card:hover {
  box-shadow: var(--shadow-md);
  transform: translateY(-2px);
}

.forecast-hour-card h3 {
  font-size: var(--font-size-base);
  margin-bottom: var(--spacing-xs);
  color: var(--color-dark-blue-700);
  font-weight: 600;
}

.forecast-hour-card .weather-icon, .forecast-hour-card img {
  width: 50px;
  height: 50px;
  margin: var(--spacing-xs) auto;
}

.forecast-hour-card p {
  font-size: var(--font-size-sm);
  color: var(--color-gray-700);
  margin: var(--spacing-xs) 0;
  line-height: 1.3;
}

#default-carousel {
  background-color: var(--color-white);
  border-radius: var(--border-radius-md);
  box-shadow: var(--shadow-sm);
  margin-top: var(--spacing-md);
  padding: 0 3rem;
}

[data-carousel-prev], [data-carousel-next] {
  background-color: var(--color-blue-500);
  opacity: .9;
  width: 3rem;
  margin: 0 -3rem;
  transition: opacity .3s;
}

[data-carousel-prev] {
  left: 0;
}

[data-carousel-next] {
  right: 0;
}

[data-carousel-prev]:hover, [data-carousel-next]:hover {
  opacity: 1;
}

#carousel-indicators button {
  background-color: var(--color-gray-300);
  transition: background-color .3s;
}

#carousel-indicators button[aria-current="true"] {
  background-color: var(--color-blue-500);
}

[data-carousel-prev] span, [data-carousel-next] span {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
}
